<template>
<div>
  <loading-spinner v-if="loading"></loading-spinner>
  <div class="page-container" v-else>
    <clientData />
    <projectData />
    <roomData />
    <div class="page-box">
      <div class="page-box-title">
        <h3>
          Metingen
        </h3>
        <div class="flex-divider"></div>
        <base-button
          @click="openCreateModal"
          class="small-on-mobile"
        >
          <base-icon
            iconRef="plus"
            position="left"
          /><span class="button-label">Nieuwe Meting</span>
        </base-button>
      </div>
      <div class="page-box-datatable-container">
        <measurements-data-list
          :dataList="currentRoomMeasurements"
          @click="dataEntryClick($event)"
        />
      </div>
    </div>
  </div>
</div>
</template>

<script>
import clientData from "./client-page-components/client-data";
import projectData from "./client-page-components/project-data";
import roomData from "./client-page-components/room-data";
import measurementsDataList from "./data-list-components/measurements-data-list.vue";
import LoadingSpinner from "./LoaderSpinner.vue";

import Firebase from "firebase";

export default {
  name: "Room",
  components: {
    clientData,
    projectData,
    roomData,
    measurementsDataList,
    LoadingSpinner
  },
  data() {
    return {
      loading: true,
      currentRoom: null,
      currentProject: null,
      currentClient: null,
      currentRoomMeasurements: []
    };
  },
  computed: {
    currentRoomArea() {
      return this.currentRoom.data().width * this.currentRoom.data().length;
    },
    currentRoomVolume() {
      return this.currentRoomArea * this.currentRoom.data().height;
    }
  },
  methods: {
    openCreateModal() {
      this.$store.commit("setModalShown", {
        dataType: "measurement",
        modalType: "create",
        shown: true
      });
    },
    dataEntryClick(value) {
      this.openMeasurement(value);
    },
    openMeasurement(targetMeasurementId) {
      this.$router.push(`/meting/${targetMeasurementId}`);
    }
  },
  watch: {
    currentClient() {
      this.$store.commit("setCurrentClientPageClient", this.currentClient);
    },
    currentProject() {
      this.$store.commit("setCurrentClientPageProject", this.currentProject);
    },
    currentRoom() {
      this.$store.commit("setCurrentClientPageRoom", this.currentRoom);
    }
  },
  mounted() {
    Firebase.firestore()
      .collection("rooms")
      .doc(this.$route.params.roomId)
      .get()
      .then(doc => {
        this.currentRoom = doc;
        this.$setPageTitle(`Ruimte - ${this.currentRoom.data().roomName}`);
        return doc;
      })
      .then(doc => {
        Firebase.firestore()
          .collection("measurements")
          .where("belongsTo", "==", doc.id)
          .where("trashed", "==", false)
          .get()
          .then(snapshot => {
            snapshot.forEach(doc => {
              this.currentRoomMeasurements.push(doc);
            });
            return;
          })
          .then(() => {
            Firebase.firestore()
              .collection("projects")
              .doc(this.currentRoom.data().belongsTo)
              .get()
              .then(doc => {
                this.currentProject = doc;
                return;
              })
              .then(() => {
                Firebase.firestore()
                  .collection("clients")
                  .doc(this.currentProject.data().belongsTo)
                  .get()
                  .then(doc => {
                    this.currentClient = doc;
                    this.$store.commit("setCurrentClientPage", {
                      client: this.currentClient,
                      project: this.currentProject,
                      room: this.currentRoom
                    });
                    this.loading = false;
                  });
              });
          });
      });
  },
  destroyed() {
    this.$store.commit("setCurrentClientPage", {});
  }
};
</script>



